.Dropdown {
    position: relative;
    width: 400px;
    padding: 20px;
    border: 1px solid black;
}

.Dropdown__menu {
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    padding-top: 20px;
    padding-bottom: 20px;
    text-align: center;
    background-color: teal;
    color: aliceblue;
}

.Dropdown__btn {
    border: none;
    padding: 10px 15px;
    background-color: teal;
    color: #fff;
    font-weight: 500;
    margin-left: 5px;
    margin-right: 5px;
    font-family: inherit;
    cursor: pointer;
    outline: none;
    box-shadow: 1px 2px 2px 0 rgb(0 0 0 / 0.5);
    transition: box-shadow 250ms linear;
}